import React, { useContext, useEffect } from 'react';
import { AuthContext } from '../../../../Contexts/AuthContext';
import styles from './UnreadConversations.module.scss';
import { MessagesPopoverContext } from '../MessagesPopoverContext';
import Message from '../Message/Message';
import ReactLoading from 'react-loading';
import { UnreadConversationsContext } from './UnreadConversationsContext';
import { reBase } from '../../../../firebase';

const UnreadConversations = () => {
    const { currentUser } = useContext(AuthContext);

    const { userConversations, setUserConversations } = useContext(UnreadConversationsContext);

    const conversationsKeys = userConversations && Object.keys(userConversations);

    //console.log(conversationsKeys);

    useEffect(() => {
        if (currentUser) {
            console.log('hello from unread convs view', currentUser.uid);

            reBase.syncState(`users/${currentUser.uid}/conversations`, {
                context: {
                    setState: ({ userConversations }) => setUserConversations({ ...userConversations }),
                    state: { userConversations }
                },
                state: 'userConversations'
            })
        }
    }, []);

    return (
        <div>
            {
                /*
                userConversationsFormatted
                    ? (
                        userConversationsFormatted.length > 0
                            ? (
                                userConversationsFormatted.map((conversation, index) => {
                                    const unreadMessages = Object
                                        .values(conversation.messages)
                                        .filter(message =>
                                            ((message.read === false) &&
                                                (message.from !== currentUser.uid)));

                                    const lastUnreadMessage = unreadMessages[unreadMessages.length - 1];

                                    return (
                                        lastUnreadMessage &&
                                        (
                                            <Message
                                                key={index}
                                                clicked={setCurrentConversationID}
                                                conversation={conversation}
                                                message={lastUnreadMessage}
                                            />
                                        )
                                    )
                                })
                            )

                            : (
                                <div>
                                    No has iniciado conversaciones con ningún usuario
                                </div>
                            )
                    )
                    : (
                        <ReactLoading
                            type='spin'
                            color="grey"
                            className={styles.loader}
                        />
                    )
                        */
            }
        </div>
    )
}

export default UnreadConversations;