import React, { useContext, useState, useEffect } from 'react';
import styles from './ConversationView.module.scss';
import { MessagesPopoverContext } from '../MessagesPopoverContext';
import { TextField } from '@material-ui/core';
import Message from './../Message/Message';
import ReactLoading from 'react-loading';
import { reBase } from '../../../../firebase';
import { AuthContext } from '../../../../Contexts/AuthContext';
import { ConversationViewContext } from './ConversationViewContext';

const ConversationView = () => {
    const { currentUser } = useContext(AuthContext);
    const { currentConversation, setCurrentConversation, currentConversationID } = useContext(ConversationViewContext);

    const [message, setMessage] = useState('');
    const [textFieldRows, settextFieldRows] = useState(1);

    //const currentConversationKeys = currentConversation && Object.keys(currentConversation);

    useEffect(() => {
        if (currentUser) {
            //console.log('currentConversationID', currentConversationID);

            reBase.bindToState(`users/${currentUser.uid}/conversations/${currentConversationID}`, {
                context: {
                    setState: ({ currentConversation }) => setCurrentConversation({ ...currentConversation }),
                    state: { currentConversation }
                },
                state: 'currentConversation'
            });
        }
    }, [currentConversationID]);

    useEffect(() => {
        if (currentConversation) {
            if (Object.values(currentConversation).length === 0) {
                reBase.post(`users/${currentUser.uid}/conversations/${currentConversationID}`, {
                    data: {
                        createdOn: (new Date()).getTime()
                    }
                });
            }
            else {
                console.log(currentConversation);
            }
        }
    }, [currentConversation]);

    const handleKeyPressed = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();

            if (message) {
                const dateSent = new Date();

                reBase.push(`users/${currentUser.uid}/conversations/${currentConversation.id}/messages`, {
                    data: {
                        content: message,
                        from: currentUser.uid,
                        read: false,
                        dateSent: dateSent.getTime()
                    }
                })


                setMessage('');
                settextFieldRows(0);
            }
        }
    };

    return (
        currentConversation &&
        (
            <div className={styles.conversationWrapper}>
                <div className={styles.conversationHeader}>
                    <div
                        className={styles.conversationHeaderImage}
                        style={{
                            background: `url(${currentConversation.userWithImage})`
                        }}
                    >
                    </div>

                    <div className={styles.conversationHeaderName}>
                        {currentConversation.userWithName}
                    </div>
                </div>

                {
                    currentConversation.messages
                        ?
                        (
                            <div>
                                hay mensajes
                        </div>
                        )
                        :
                        (
                            <div>
                                No hay mensajes
                        </div>
                        )
                    /*
    
                        <div className={styles.conversationMessages}>
                            {
                                Object.values(currentConversation.messages)
                                    .map((message, index) => (
                                        <Message
                                            key={index}
                                            message={message}
                                            conversation={currentConversation}
                                        />
                                    ))
                            }
                        </div>
    
                    </div>
                )
                : (
                    <ReactLoading
                        type='spin'
                        color="grey"
                    />
                )
                    */
                }

                <div className={styles.textfieldWrapper}>
                    <TextField
                        fullWidth
                        placeholder='Escribe un mensaje'
                        multiline
                        rows={textFieldRows}
                        rowsMax={4}

                        value={message}
                        onChange={e => setMessage(e.currentTarget.value)}
                        onKeyDown={e => handleKeyPressed(e)}

                        inputProps={{
                            style: { fontSize: '14px', padding: '.25rem' }
                        }}
                    />
                </div>
            </div>
        )
    );
}

export default ConversationView;